.app {
  color: red;
}

BODY {
  background-color: #cacaca;  
  user-select: none;
}

#gallery {
  margin-top: 3em;
  background-color: #cacaca;
  
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

/* Cards should be square, with height equal to their width, no matter what their contents.
 * Width should be 85% of the width of the gallery, and height should be equal to the width.
 */
.card {
  border: 1px solid #000;
  margin: 2em;
  margin-top: 25%;
  margin-bottom: 25%;
  
  width: 85vw;
  min-width: 85vw;
  max-width: 85vw;

  height: 85vw;
  min-height: 85vw;
  max-height: 85vw;
  
/*   height: 300px;
  min-height: 300px;
  max-height: 300px;
 */
/*   flex-basis: 30%; */

/*   display: flex;
  justify-content: center;
  align-items: top; */
}

.card IMG {
  max-width: 100%;
  max-height: 100%;
}
